 @import url('https://fonts.googleapis.com/css?family=Press+Start+2P');

html, body{
  margin: 0;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
}

#root {
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: 'Press Start 2P', cursive;
  background-image: url('./background.jpg');
  background-size: cover;
  background-repeat: no-repeat;
}


.HudAndGrid {
  padding: 5vh;
  margin: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  background-image: url('./background.jpg');

}

.Footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.Hud {
  background: black;
  color: white;
  border: .5em solid white;
  padding: .5em;
  border-radius: 0px 10px 10px 0px;
  width: 500px;
}

.TitleScreen {
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  width: 100%;
  margin: 0;
  background: black;
  color: white;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
  -ms-flex-direction: column;
      flex-direction: column;
}

.Title {
  font-size: 3em;
}

.heroImg {
  height: 30vh;
  opacity: 3;
  z-index: 2;
}

.PlayButton {
  -webkit-animation-name: blink;
          animation-name: blink;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes blink {
  0% { color: black;}
  50% { color: white;}
  100% { color: black;}
}

@keyframes blink {
  0% { color: black;}
  50% { color: white;}
  100% { color: black;}
}

.Hitbox1 {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 50px;
  width: 50px;
  background-color: green;
  z-index: 2;
  position: absolute;
  color: white;
  opacity: 0;
}

.Hitbox2 {
  height: 100px;
  width: 100px;
  background-color: transparent;
  z-index: 2;
  border: 3px solid green;
  position: absolute;
  opacity: 0;
 }



