.Dungeon {
  border-bottom: .5em solid white;
  border-top: .5em solid white;
  border-left: .5em solid white;
  border-radius: 10px 0px 0px 10px;
  display: -ms-flexbox;
  display: flex;
}

.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  color: red;
}


.cell {
  height: 1.2em;
  width: 1.2em;
  border: 1px solid transparent;
  background: black;
}

.hidden {
  background: black !important;
}

.floor {
  background-image: url("../gameElements/ice2.png");
}
.hero {
  background-image: url("../gameElements/hero.png"), url("../gameElements/ice2.png");
  background-size: contain;
}

.portal {
  background-image:url("../gameElements/dngn_blue_fountain.png"), url("../gameElements/ice2.png");
  background-size: contain;
}
.ArachnaDemos {
  background-image:url("../gameElements/redback.png"), url("../gameElements/ice2.png");
  background-size: contain;
}

.Abomination {
  background-image:url("../gameElements/abomination_large1.png"), url("../gameElements/ice2.png");
  background-size: contain;
}
.Skraw{
  background-image:url("../gameElements/hell_knight.png"), url("../gameElements/ice2.png");
  background-size: contain;
}

.Mygo {
  background-image:url("../gameElements/giant_mosquito.png"), url("../gameElements/ice2.png");
  background-size: contain;
}
.health {
  background-image:url("../gameElements/i-heal.png"), url("../gameElements/ice2.png");
  background-size: contain;
}

.Boss {
  overflow: visible;
  background-image: url("../gameElements/ice2.png");
}

.BossArea {
  background-image: url("../gameElements/ice2.png");
}

img {
  position: absolute;
  z-index: 3;

}
.weapon {
  background-image:url("../gameElements/unseen_item.png"), url("../gameElements/ice2.png");
  background-size: contain;
}
.wall {
  background-image: url("../gameElements/cobble_blood8.png");
}



